<template>
  <el-container id="container">
    <el-main>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-card shadow="always">
            <div slot="header">
              <span>资源管理</span>
            </div>
            <resource-tree :showActionButton="true" :checkbox="false" :ref="refs.tree"
                           @add="add" @edit="edit" @remove="remove"
                           @clickNode="clickNode"></resource-tree>
          </el-card>
        </el-col>
        <el-col :span="14">
          <resource-form :content="form.content" @afterAction="afterSubmit"
                         :action="form.action" :show.sync="form.show"
                         :disabled="form.disabled"></resource-form>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>
<script>
  import ResourceForm from '@components/resources/ResourceForm';
  import ResourceTree from '@components/resources/ResourceTree';

  /**
   * 节点类型
   * @type {{CLIENT: string, RESOURCES: string}}
   */
  const NODE_TYPE = {
    CLIENT: 'C',
    RESOURCES: 'R'
  };

  export default {
    components: {
      'resource-form': ResourceForm,
      'resource-tree': ResourceTree
    },
    data() {
      return {
        refs: {
          tree: 'tree',
          form: 'form'
        },
        form: {
          content: {},
          show: false,
          disabled: true,
          action: 'detail'
        }
      };
    },
    methods: {
      /**
       * 新增资源
       * @param {Object} event
       * @param {Object} data
       * @param {Object} node
       */
      async add(event, data, node) {
        // 阻止冒泡事件，防止触发点击节点事件
        event.stopPropagation();

        if (data.nodeType === NODE_TYPE.CLIENT) {
          // 如果节点类型为系统
          let clientId = data.original.clientId;

          // 选中当前节点
          this.$refs[this.refs.tree].getElementTreeRef().setCurrentKey(clientId);

          // 获取系统名称、系统ID和最新展示顺序
          const [client, dispOrder] = await Promise.all([
            this.$api.clients.getClientsByClientId(clientId),
            this.$api.resources.getLastestDisplayOrder({
              clientId
            })
          ]);

          // 设置表单内容
          this.form.content = {
            clientId: client.data.data.clientId,
            clientName: client.data.data.clientName,
            dispOrder: dispOrder.data.data
          };

          // 修改表单行为
          this.form.show = true;
          this.form.action = 'add';
          this.form.disabled = false;
        } else {
          // 如果当前节点为资源
          let pResourceId = data.original.resourceId;
          let clientId = data.original.clientId;

          // 选中当前节点
          this.$refs[this.refs.tree].getElementTreeRef().setCurrentKey(pResourceId);

          // 获取资源信息和展示顺序
          const [resource, dispOrder] = await Promise.all(
            [this.$api.resources.getResourceByResourceId(pResourceId),
              this.$api.resources.getLastestDisplayOrder({
                clientId,
                pResourceId
              })
            ]);

          // 设置表单内容
          this.form.content = {
            clientId: resource.data.data.clientId,
            clientName: resource.data.data.clientName,
            dispOrder: dispOrder.data.data,
            pResourceId: pResourceId,
            parentResourceId: pResourceId
          };

          // 修改表单行为
          this.form.show = true;
          this.form.action = 'add';
          this.form.disabled = false;
        }
      },
      /**
       * 删除资源
       * @param {Object} event
       * @param {Object} data
       * @param {Object} node
       */
      remove(event, data, node) {
        console.log(node);
        // 阻止冒泡事件，防止触发点击节点事件
        event.stopPropagation();
        if (node.level !== 1 && !node.isLeaf) {
          this.$message({
            showClose: true,
            message: '该节点下存在子节点，不允许删除！',
            type: 'error'
          });
          return;
        }
        // 选中当前节点
        this.$refs[this.refs.tree].getElementTreeRef().setCurrentKey(data.original.resourceId);

        // 弹出询问框
        this.$confirm(`此操作将删除资源【${data.label}】，是否继续？`, '删除', {
          type: 'warning'
        }).then(async () => {
          // 获取当前系统节点，用于后续刷新
          let clientId = data.original.clientId;
          try {
            // 调用删除资源接口，删除当前资源以及以下资源
            const response = await this.$api.resources.removeResource(data.original.resourceId);
            if (response.data.meta.success == true) {
              this.$message({
                showClose: true,
                message: response.data.meta.message,
                type: 'success'
              });

              // 删除成功之后刷新节点
              let pResourceId = node.parent.nodeType === NODE_TYPE.CLIENT ? null
                  : node.data.original.pResourceId;
              await this.refreshTreeNode(clientId, pResourceId);
              this.form.show = false;
            } else {
              console.error(response.data.meta.message);
              // 弹框提示删除错误
              this.$message({
                showClose: true,
                message: response.data.meta.message,
                type: 'error'
              });

              // 修改表单行为
              this.form.show = false;
            }
          } catch (error) {
            console.error(error);

            // 弹框提示删除错误
            this.$message({
              showClose: true,
              message: error.data.meta.message,
              type: 'error'
            });

            // 修改表单行为
            this.form.show = false;
          }
        });
      },
      /**
       * 编辑资源
       * @param {Object} event
       * @param {Object} data
       * @param {Object} node
       */
      async edit(event, data, node) {
        event.stopPropagation();
        this.form.content =
          (await this.$api.resources.getResourceByResourceId(data.original.resourceId)).data.data;
        this.form.show = true;
        this.form.disabled = false;
        this.form.action = 'edit';
      },
      /**
       * 点击节点
       * @param {Object} data
       * @param {Object} node
       * @param {Object} self
       */
      async clickNode(data, node, self) {
        if (data.nodeType === NODE_TYPE.RESOURCES) {
          this.form.content =
            (await this.$api.resources.getResourceByResourceId(data.original.resourceId)).data.data;
          this.form.action = 'detail';
          this.form.show = true;
          this.form.disabled = true;
        } else {
          this.form.show = false;
        }
      },
      /**
       * 刷新树节点
       * @param {String} clientId
       * @param {String} pResourceId
       * @return {Promise<number>}
       */
      async refreshTreeNode(clientId, pResourceId = null) {
        const response = await this.$api.resources.getResourcesForTree({
          clientId: clientId,
          pResourceId: pResourceId
        });

        let nodes = [];
        for (const resource of response.data.data) {
          nodes.push({
            id: resource.resourceId,
            label: resource.resourceName,
            leaf: resource.childrenCount === 0,
            nodeType: 'R',
            original: resource
          });
        }
        // 更新父节点下的节点
        let nodeId = pResourceId || clientId;
        let node = this.$refs[this.refs.tree].getElementTreeRef().getNode(nodeId);
        node.expand();
        this.$refs[this.refs.tree].getElementTreeRef().updateKeyChildren(pResourceId || clientId,
          nodes);
        return 1;
      },
      async afterSubmit({clientId, resourceId, pResourceId = null}) {
        // 刷新新增/编辑的父节点
        await this.refreshTreeNode(clientId, pResourceId);

        // 选中新增/编辑的节点
        this.$refs[this.refs.tree].getElementTreeRef().setCurrentKey(resourceId);

        // 获取节点信息
        let node = this.$refs[this.refs.tree].getElementTreeRef().getNode(resourceId);

        // 触发点击事件
        this.$refs[this.refs.tree].getElementTreeRef().$emit('node-click', node.data);
      }
    }
  };

</script>

<style scoped>
  #container {
    margin-top: -10px;
  }
</style>

