<template>
  <el-card shadow="always" v-show="show === true">
    <div slot="header">
      <span>资源信息</span>
    </div>
    <el-form :ref="ref" :model="content" :rules="rules" label-width="100px"
             :disabled="disabled">
      <el-form-item label="归属系统">
        <el-input v-model="content.clientName" disabled="disabled"></el-input>
      </el-form-item>
      <el-form-item label="资源名称" prop="resourceName">
        <el-input v-model="content.resourceName"></el-input>
      </el-form-item>
      <el-form-item label="资源类型" prop="resourceType">
        <el-radio-group v-model="content.resourceType">
          <el-radio v-for="token of preservedData.resourceType" :key="token.VALUE"
                    :label="token.VALUE">{{token.TEXT}}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="元素类型" v-if="content.resourceType &&
              content.resourceType === 'ELEMENT'" prop="elementType">
        <el-select  v-model="content.elementType">
          <el-option  v-for="token of preservedData.elementType" :key="token.VALUE"
                     :label="token.TEXT" :value="token.VALUE">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="HTTP类型" v-if="content.resourceType &&
              content.resourceType === 'API'" prop="elementType">
        <el-select  v-model="content.elementType">
          <el-option  v-for="token of preservedData.httpMethodType" :key="token.VALUE"
                      :label="token.TEXT" :value="token.VALUE">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="访问路径">
        <el-input v-model="content.resourceUrl"></el-input>
      </el-form-item>
      <el-form-item label="权限编码" prop="privilegeCode">
        <el-input v-model="content.privilegeCode"></el-input>
      </el-form-item>
      <el-form-item label="显示顺序">
        <el-input-number v-model="content.dispOrder" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="content.resourceDesc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
  export default {
    props: ['content', 'show', 'action', 'disabled'],
    data() {
      return {
        ref: 'form',
        rules: {
          resourceName: [{
            required: true,
            message: '请输入资源名称',
            trigger: 'blur'
          }],
          resourceType: [{
            required: true,
            message: '请选择资源类型',
            trigger: 'change'
          }],
          privilegeCode: [{
            required: true,
            trigger: 'blur',
            validator: (rule, value, callback) => {
              if (!this.content.privilegeCode) {
                callback(new Error('请输入权限编码'));
              } else
              if(this.content.privilegeCode
                  && /[\u4e00-\u9fa5]+/.test(this.content.privilegeCode)){
                callback(new Error('请输入非中文字符'));
              } else {
                callback();
              }
            }
          }],
          elementType: [{
            required: true,
            message: '请选择类型',
            trigger: 'change'
          }]
        },
        preservedData: {
          elementType: null,
          resourceType: null,
          httpMethodType: null
        }
      };
    },
    methods: {
      cancelAction () {
        this.$emit('update:show', false)
      },
      async fetchPreservedData() {
        try {
          this.preservedData.resourceType = (await this.$api.resources.getResourceType()).data.data;
          this.preservedData.elementType = (await this.$api.resources.getElementType()).data.data;
          this.preservedData.httpMethodType = (await this.$api.resources.getHttpMethodType()).data.data;
        } catch (error) {
          this.$message({
            showClose: true,
            message: error.data.meta.message,
            type: 'error'
          });
        }
      },
      onSubmit() {
        this.$refs[this.ref].validate(async (valid) => {
          if (valid) {
            try {
              // 根据表单内是否有主键判断是新增还是编辑
              let task = await (this.content.resourceId === undefined
                ? this.$api.resources.addResource(this.content)
                : this.$api.resources.editResource(this.content));

              let result = task.data;
              // 弹出提示
              this.$message({
                showClose: true,
                message: result.meta.message,
                type: 'success'
              });

              // 从响应中获取当前新增/编辑的资源ID，父资源ID，系统ID
              this.$emit('afterAction', {
                resourceId: result.data.resourceId,
                clientId: result.data.clientId,
                pResourceId: result.data.pResourceId || null
              });
            } catch (error) {
              console.dir(error);
              this.$message({
                dangerouslyUseHTMLString: true,
                showClose: true,
                message: error.data.meta.message,
                type: 'error'
              });
            }
          }
          return false;
        });
      }
    },
    created() {
      this.fetchPreservedData();
    },
    watch: {
      action(newVal, oldVal) {
        this.$refs.form.resetFields();
      },
      'content.resourceType': function(newVal, oldVal) {
        //这个地方设计有点问题，详情、编辑用的表单是同一个实例，为了维护接口和元素类型的资源，切换radio时清空elementType，不得已做了以下控制：
        if(this.action!=='detail' &&this.content.elementType){
          const typeData = newVal==='ELEMENT'?this.preservedData.elementType:this.preservedData.httpMethodType
         const  element = typeData.filter(d=>{
           return d.VALUE === this.content.elementType});
          if(element.length !==1){
            this.content.elementType = ''
          }
        }
      }
    }
  };

</script>

