<template>
  <el-tree :ref="ref" :data="tree.data" :load="asyncLoadTreeNode" :lazy="true"
           :highlight-current="true" accordion node-key="id"
           :show-checkbox="checkbox"
           :expand-on-click-node="false" :props="tree.defaultProps"
           @node-click="clickNode"
           draggable
           v-loading="loading"
           :allow-drop="allowDrop"
           :allow-drag="allowDrag"
           @node-drop="handleDrop">
    <span class="custom-tree-node" slot-scope="{ node, data }">
      <span>{{ node.label }}</span>
      <span v-if="showActionButton">
        <el-button type="text" size="mini"
                   @click="(event) => add(event, data, node)">新增</el-button>
        <el-button type="text" size="mini" v-if="data.nodeType === 'R'"
                   @click="(event) => edit(event, data, node)">修改</el-button>
        <el-button type="text" size="mini" v-if="data.nodeType === 'R'"
                   @click="(event) => remove(event, data, node)">删除</el-button>
      </span>
    </span>
  </el-tree>
</template>

<script>
  import constants from '@common/constants/constants';

  export default {
    props: {
      checkbox: {
        type: Boolean,
        required: false,
        default: true
      },
      showActionButton: {
        type: Boolean,
        required: false,
        default: false
      },
      rootId: {
        type: String,
        required: false,
        default: null
      },
    },
    data() {
      return {
        loading: false,
        ref: 'tree',
        tree: {
          data: [],
          defaultProps: {
            isLeaf: 'leaf'
          }
        },
      };
    },
    methods: {
      allowDrop(draggingNode, dropNode, type) {
        return dropNode.level !== 1 && (draggingNode.level !== dropNode.level || type === 'inner')
      },
      allowDrag(draggingNode) {
        return draggingNode.level !== 1
      },
      async handleDrop(draggingNode, dropNode, dropType, ev) {
        let currNodeData = draggingNode.data.original
        if (dropNode.level === 2 && dropType !== 'inner') {
          currNodeData.parentResourceId = ''
        } else if (dropType === 'inner') {
          dropNode.isLeaf = false
          currNodeData.parentResourceId = dropNode.data.original.resourceId
        } else if (dropType === 'before' || dropType === 'after') {
          currNodeData.parentResourceId = dropNode.data.original.pResourceId
        }
        try {
          this.loading = true
          let res = await this.$api.resources.editResource(currNodeData)
          if (res.data.meta.success === true) {
            this.$message({
              message: '成功!',
              type: 'success',
              time: 1000
            });
          } else {
            this.$message({
              message: '调整失败!',
              type: 'error'
            });
          }
          this.loading = false
        } catch (error){
          this.loading = false
          console.error(error)
          this.$message({
            showClose: true,
            message: error.data.meta.message,
            type: 'error'
          });
        }
      },
      add(event, data, node) {
        this.$emit('add', event, data, node);
      },
      edit(event, data, node) {
        this.$emit('edit', event, data, node);
      },
      remove(event, data, node) {
        this.$emit('remove', event, data, node);
      },
      /**
       * 异步加载资源树节点
       * @param {Object} node
       * @param {function} resolve
       */
      async asyncLoadTreeNode(node, resolve) {
        if (node.level !== 0) {
          let nodes = [];
          let clientId = node.data.nodeType === 'C' ? node.data.id : null;
          let pResourceId = node.data.nodeType === 'R' ? node.data.id : null;
          let response = await this.$api.resources.getResourcesForTree({
            clientId: clientId,
            pResourceId: pResourceId
          });

          for (const resource of response.data.data) {
            nodes.push({
              id: resource.resourceId,
              label: resource.resourceName,
              leaf: resource.childrenCount === 0,
              nodeType: 'R',
              original: resource
            });
          }
          resolve(nodes);
        }
      },
      clickNode(data, node, self) {
        this.$emit('clickNode', data, node, self);
      },
      /**
       * 预存资源类型、元素类型数据
       */
      async initTreeNodes() {
        this.tree.data = [];
        let response = await this.$api.clients.getClientsForTree({
          state: constants.state.VALID,
          clientId: this.rootId
        });
        for (const client of response.data.data) {
          this.tree.data.push({
            id: client.clientId,
            label: client.clientName,
            leaf: client.resourceCount === 0,
            nodeType: 'C',
            original: client
          });
        }
      },
      getCheckedNodes() {
        return this.$refs[this.ref].getCheckedNodes();
      },
      getElementTreeRef() {
        return this.$refs[this.ref];
      }
    },
    created() {
      this.initTreeNodes();
    }
  };
</script>

<style>
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 12px;
  }
</style>
